body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  color: rgb(232, 193, 160);
  font-size: 1rem;
  margin-top: 2rem;
}

@media(min-width: 600px) {
  h1 {
    font-size: 2rem;
    margin-top: 2rem;
  }
}

.tooltip-row {
  margin: 0;
  
}
a.table-title {
  color:rgb(232, 193, 160);
  margin-left: 5px;
}

h3 {
  font-size: 1rem;
  margin-top: 2rem;
}
@media(min-width: 600px) {
  h3 {
    font-size: 2rem;
  }
}

table.covid-table {
  margin-bottom: 2rem;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
  font-size: 1rem;
}
@media(min-width: 600px) {
  table.covid-table {
    font-size: 1.5rem;
  }
}


td.covid-table {
  border-top: 1px solid white;
}